import React, { useEffect } from 'react';
import './css/index.css';
import { motion } from 'framer-motion';
import confetti from 'canvas-confetti';
import cake from '../assets/cake.png';
import { Link } from 'react-router-dom';

const Home = () => {
  useEffect(() => {
    const createHeart = () => {
      const heart = document.createElement('div');
      heart.classList.add('heart');

      // Random position across entire width of window
      const leftPosition = Math.random() * window.innerWidth;
      heart.style.left = leftPosition + 'px';
      heart.style.animationDuration = Math.random() * 1 + 0.5 + 's'; // Even faster animation
      heart.innerHTML = '';
      heart.style.zIndex = '1';
      // Start hearts from just above the viewport
      heart.style.top = '-20px';
      document.body.appendChild(heart);

      const mainContainer = document.querySelector('.main-container');
      const containerRect = mainContainer.getBoundingClientRect();

      const checkBlur = () => {
        const heartRect = heart.getBoundingClientRect();

        if (
          heartRect.bottom > containerRect.top &&
          heartRect.top < containerRect.bottom &&
          heartRect.right > containerRect.left &&
          heartRect.left < containerRect.right
        ) {
          heart.style.filter = 'blur(5px)';
        } else {
          heart.style.filter = 'none';
        }
      };

      const intervalId = setInterval(checkBlur, 16); // Even more frequent checks (60fps)

      setTimeout(() => {
        clearInterval(intervalId);
        heart.remove();
      }, 1500); // Even shorter lifetime
    };

    // Initial celebration effects - immediate
    confetti({
      particleCount: 150,
      spread: 80,
      origin: { y: 0.6 },
      colors: ['#ff69b4', '#ff1493', '#ff69b4', '#ffffff']
    });

    // Start creating hearts immediately
    const heartInterval = setInterval(createHeart, 150); // Even faster interval
    return () => clearInterval(heartInterval);

  }, []);

  const createConfetti = () => {
    confetti({
      particleCount: 15,
      spread: 60,
      origin: { y: 0.3 },
      colors: ['#ff69b4', '#ff1493', '#ff69b4', '#ffffff'],
      zIndex: 1,
      position: 'absolute'
    });
  };

  const [confettiInterval, setConfettiInterval] = React.useState(null);

  const handleCakeClick = () => {
    createConfetti();
  };

  const containerVariants = {
    hidden: { 
      scale: 0,
      opacity: 0,
    },
    visible: {
      scale: 1,
      opacity: 1,
      transition: {
        type: "spring",
        duration: 0.15, // Much faster animation
        bounce: 0.2 // Even less bounce for instant feel
      }
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <motion.div 
          className="main-container"
          initial="hidden"
          animate="visible"
          variants={containerVariants}
        >
          <img
            src={cake}
            alt="Birthday Cake"
            className="cake-image"
            onMouseEnter={() => {
              const interval = setInterval(createConfetti, 50); // Even faster confetti
              setConfettiInterval(interval);
            }}
            onMouseLeave={() => {
              if (confettiInterval) {
                clearInterval(confettiInterval);
                setConfettiInterval(null);
              }
            }}
            onClick={handleCakeClick}
          />
          
          <h1 className="birthday-text">Happy 16 Mi amor!</h1>

          <div className="navigation-section">
            <Link to="/2" style={{ textDecoration: 'none' }}>
              <button className="next-button">
                Next Page
              </button>
            </Link>
          </div>
        </motion.div>
      </header>
    </div>
  );
};

export default Home;
